.Error-container {
    background-color: #f8d7da;
    color: #721c24;
    padding: 15px;
    border: 1px solid #f5c6cb;
    border-radius: 8px;
    max-width: 500px;
    margin: 10px auto;
    font-family: 'Arial', sans-serif;
    text-align: center;
  }
  
  .Error-message {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
  }
  